<template>
  <a-layout class="container">
      <a-result 
        status="success" 
        title="您已成功发布表单" 
        sub-title="链接地址如下"
      >
        <template #extra>
          <a-layout-content class="main">
            <a-typography-paragraph :copyable="{ text: url }">点击复制链接：{{ url }}</a-typography-paragraph>
            <a-button type="primary" class="open-button" @click="openForm">打开表单</a-button>
            <qrcode-vue :value="url" :size="size" id="qrcode" />
            <a-button class="download-button" @click="downloadQrCode">下载</a-button>
          </a-layout-content>
        </template>
      </a-result>
    
  </a-layout>

</template>

<script setup>
import QrcodeVue from 'qrcode.vue';
import { ref } from "vue";
// ***********修改************ //
// const urlParams = new URL(window.location.href);
// const randUrl = urlParams.searchParams.get("randUrl");
const hashValue = window.location.hash;
const [prefix, suffix] = hashValue.split('?randUrl='); // 拆分字符串，获取前缀和后缀
const randUrl = suffix; // 等号后面的部分即为参数值
console.log(hashValue)
// const randUrl = params.get("randUrl");
// const randUrl = "123123123"
// ***********修改*********** //
// 链接地址
const url = ref(randUrl);
// 二维码大小
const size = ref(150);

// 二维码下载
function downloadQrCode() {
  // 构造标签模拟点击
  const link = document.createElement('a')
  link.download = `qrcode.png`
  link.href = document.getElementById('qrcode').toDataURL('image/png');
  link.click()
  link.remove();
}

function openForm() {
  window.location.href = url.value;
}
</script>

<style scoped>
.container {
  height: 100%;
  overflow: hidden;
}

.main {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.open-button {
  margin: 10px;
}

.download-button{
  margin: 2px;
}
</style>